  setup() {
    const state = reactive({
      musicList: []
    });
    function changeCount(num) {
      if (num >= 100000000) {
        return (num / 100000000).toFixed(1) + '亿';
      }
      if (num >= 10000) {
        return (num / 10000).toFixed(1) + '万';
      }
    }
    onMounted(async () => {
      let res = await getMusicList();
      state.musicList = res.data.result;
    });
    return {
      state,
      changeCount
    };
  }
};