import { getBanner } from '@/request/api/home';
import { onMounted, reactive } from 'vue';
export default {
  setup() {
    const state = reactive({
      imges: ['https://fastly.jsdelivr.net/npm/@vant/assets/apple-1.jpeg', 'https://fastly.jsdelivr.net/npm/@vant/assets/apple-2.jpeg']
    });
    onMounted(async () => {
      let {
        data
      } = await getBanner();
      state.imges = data.banners;
    });
    return {
      state
    };
  }
};