import { mapState, mapMutations } from "vuex";
import MusicDetail from "@/components/item/MusicDetail";
export default {
  data() {
    return {
      interVal: 0
    };
  },
  components: {
    MusicDetail
  },
  computed: {
    ...mapState(["playListIndex", "playList", "isbtnShow", "detailShow"])
  },
  mounted() {
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
  },
  updated() {
    this.$store.dispatch("getLyric", this.playList[this.playListIndex].id);
    this.updateTime();
    this.addDuration();
  },
  methods: {
    ...mapMutations(["updateisBtnShow", "updataDetailShow", "updateCurrentTime", "updateDuration"]),
    addDuration: function () {
      this.updateDuration(this.$refs.audio.duration);
    },
    play() {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play();
        this.updateisBtnShow(false);
        this.updateTime();
      } else {
        this.$refs.audio.pause();
        this.updateisBtnShow(true);
        clearInterval(this.interVal); //暂停清除定时器
      }
    },

    updateTime: function () {
      this.interVal = setInterval(() => {
        this.updateCurrentTime(this.$refs.audio.currentTime);
      }, 1000);
    }
  },
  watch: {
    playListIndex() {
      this.$refs.audio.autoplay = true;
      if (this.$refs.audio.pause) {
        this.updateisBtnShow(false);
      }
    },
    playList() {
      if (this.isbtnShow) {
        this.$refs.audio.autoplay = true;
        this.updateisBtnShow(false);
      }
    }
  }
};