import service from '..'
export const getBanner= () => service({method:'get',url:'/banner?type=2'})
export const getMusicList= ()=> service({method:'get',url:'/personalized?limit=10'})
export const getSearchMusic =(data) => service({method:"GET",url:`/search?keywords=${data}`})
export function getPhoneLogin(data){
    return service({
        method:"GET",
        url:`/login/cellphone?phone=${data.phone}&password=${data.password}`
    })
}
